<template>
<div class="container mt-5">
    <h1 class="mb-5">Notas</h1>

    <b-alert :show="dismissCountDown" 
    dismissible 
    :variant="mensaje.color" 
    @dismissed="dismissCountDown=0" 
    @dismiss-count-down="countDownChanged"
    >{{mensaje.texto}}</b-alert>


    <form @submit.prevent="editarNota(notaEditar._id)" v-if="modo_editar">
        <h3>Editar nota</h3>
        <input type="text" class="form-control my-2" placeholder="Nombre" v-model="notaEditar.nombre">
        <input type="text" class="form-control my-2" placeholder="Descripcion" v-model="notaEditar.descripcion">
        <b-button class="btn-warning my-2 mr-2" type="submit">
            Editar
        </b-button>
        <b-button class="btn-info my-2" @click="modo_editar=false">
            Cancelar
        </b-button>
    </form>
    <form @submit.prevent="agregarNota()" v-if="!modo_editar">
        <h3>Agregar nueva nota</h3>
        <input type="text" class="form-control my-2" placeholder="Nombre" v-model="nota.nombre">
        <input type="text" class="form-control my-2" placeholder="Descripcion" v-model="nota.descripcion">
        <b-button class="btn-info my-2" type="submit">
            Agregar
        </b-button>
    </form>

    <table class="table">
        <thead>
            <tr>
                <th scope="col">#</th>
                <th scope="col">Nombre</th>
                <th scope="col">Descripcion</th>
                <th scope="col">Acciones</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(nota,index) in notas" :key="index">
                <th scope="row">{{nota._id}}</th>
                <td>{{nota.nombre}}</td>
                <td>{{nota.descripcion}}</td>
                <td>
                    <b-button class="btn-warning mr-2 btn-sm" @click="activarEdicion(nota._id)">Editar</b-button>
                    <b-button class="btn-danger btn-sm" @click="eliminarNota(nota._id)">Eliminar</b-button>
                </td>
            </tr>
        </tbody>
    </table>
</div>
</template>

<script>
export default {
    name: "Nota",
    data() {
        return {
            notas: [],
            dismissCountDown: 0,
            dismissSecs: 8,
            mensaje: {
                texto: '',
                color: ''
            },
            nota:{
                nombre: '',
                descripcion:''              
            },
            notaEditar:{},
            modo_editar:false
        };
    },
    created() {
        this.listarNotas();
    },
    methods: {
        listarNotas() {
            this.axios.get("/notas")
                .then((res) => {
                    this.notas = res.data;
                })
                .catch((error) => {
                    console.log(error.response);
                });
        },
        agregarNota(){
            this.axios.post('/nueva-nota', this.nota)
                .then( res =>{
                    this.notas.push(res.data);
                    this.nota.nombre='';
                    this.nota.descripcion='';

                    this.mensaje.color='success';
                    this.mensaje.texto='Nota agregada satisfactoriamente!';
                    this.showAlert();
                })
                .catch(err =>{
                    if(err.response.data.error.errors.nombre.message){
                        this.mensaje.texto = err.response.data.error.errors.nombre.message;
                    }else{
                        this.mensaje.texto = "Hubo un error al agregar la nota";
                    }
                    this.mensaje.color='danger'; 
                    this.showAlert();
                });
        },
        eliminarNota(id){
            this.axios.delete(`/nota/${id}`)
                .then(res => {
                    let index = this.notas.findIndex(item => item._id === res.data._id);
                    this.notas.splice(index,1);

                    this.mensaje.color='warning';
                    this.mensaje.texto='Nota eliminada satisfactoriamente!';
                    this.showAlert();

                })
                .catch(err => {
                    console.log(err);
                });
        },
        activarEdicion(id){
            this.modo_editar=true;
            this.axios.get(`/nota/${id}`)
                .then(res=>{
                    this.notaEditar=res.data;
                })
                .catch(err=>{
                    console.log(err);
                });
        },
        editarNota(id){
            this.axios.put(`/nota/${id}`, this.notaEditar)
                .then(res=>{

                    let index = this.notas.findIndex(item => item._id === res.data._id);
                    this.notas[index].nombre=res.data.nombre;
                    this.notas[index].descripcion=res.data.descripcion;
                    this.mensaje.color='success';
                    this.mensaje.texto='Nota editada satisfactoriamente!';
                    this.showAlert();
                    this.modo_editar=false;

                })
                .catch(err=>{
                    console.log(err);
                });

        },
        countDownChanged(dismissCountDown) {
            this.dismissCountDown = dismissCountDown;
        },
        showAlert() {
            this.dismissCountDown = this.dismissSecs;
        }

    },
};
</script>
